export default {		
	data() {
		return {
			title: 'Hello',
			form:{
				teacher:'苹果老师'
			},
			isLogin:true,
			widths:[10,0],
			width1:0,
			flag:false
		}
	},
	watch:{
		widths:function (oldval,newval){
			console.log(oldval)
		},
		deep:true
	},
	onLoad() {
		uni.authorize({
			scope:'userInfo'
		})
	},
	methods: {
		getuserinfo(){
			// uni.getUserInfo({
			// 	success:(res)=>{
			// 		console.log(res)
			// 	},
			// 	fail:(err)=>{
			// 		console.log(err)
			// 	}
			// })
			// wx.login({
			// 	success:(res)=>{
			// 		console.log(res)
			// 	}
			// }) 
		},
		goRouter(){
			if(this.isLogin){
				//查看个人资料
				// this.goInfo(2)
				uni.navigateTo({
					url:"../../components/msg/userinfo"
				})
			}else{
				//登陆
				uni.navigateTo({
					url:"../my/login"
				})
			}
			
		},
		lookDetail(){
			console.log("点击")
			this.flag=false
			// this.widths[0]=10
			//运行当中===>线路详情
			uni.navigateTo({
				url:"../../components/msg/linedetails"
			})
		},
		touchStart(e,index){
			console.log("拖动小车")
			this.flag=true
			// console.log(e)
			let page=e.touches[0].pageX
			// console.log(page)
			// console.log(index)
			switch(index){
				case 0:
				// this.widths[index]=page
				this.width1=page
				// console.log(this.widths[0])
			}
			
		},
		touchEnd(){
			if(this.flag){
				console.log("查看线路安全报告")
				uni.navigateTo({
					url:"../../components/msg/security"
				})
			}
			
		},
		goInfo(index){
			switch(index){
				case 1:
					// 行程记录
					uni.navigateTo({
						url:"../../components/msg/Itineraryrecord"
					})
					break
				case 2:
					this.goRouter()
					break
			}
		}
	}
}
